@import url(https://fonts.googleapis.com/css2?family=Inconsolata&display=swap);
body{margin:0;font-family:Inconsolata,monospace;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}html,body{height:100%;width:100%;scroll-behavior:smooth}#root{display:flex;height:100%;flex-direction:column;align-items:center;justify-content:space-between;--bg-color: black;--font-color: lightslategray;--hl-color: #97d66f;--fg-color: #292f34;color:var(--font-color);background-color:var(--bg-color)}button{background-color:var(--bg-color);color:var(--font-color);border:2px solid var(--font-color);padding:10px;font-family:Inconsolata,monospace;font-weight:700;margin:10px;cursor:pointer}button.mini{font-size:12pt;border:none;padding:5px;margin:0}button.mini.selected{color:var(--hl-color)}button:hover{color:var(--hl-color);border-color:var(--hl-color)}a{color:var(--font-color);text-decoration:none;transition:color .1s ease}a span{font-size:smaller;margin:2.5px}a:hover{color:var(--hl-color)}.hidden{opacity:0;pointer-events:none}
table{font-size:16pt;font-weight:800}table button{font-size:21pt}.wrong{color:var(--hl-color)}
.test{display:flex;flex-direction:column;justify-content:center;font-weight:700;height:99.8%;width:50%}.timer{font-size:21pt;margin:5px;color:var(--hl-color)}.box{font-size:21pt;overflow:hidden;height:93px;display:flex;flex-wrap:wrap;align-items:center;-webkit-user-select:none;-ms-user-select:none;user-select:none}.box .word{position:relative;margin:0 5px 2px}.box .word.wrong{-webkit-text-decoration:2px underline var(--hl-color);text-decoration:2px underline var(--hl-color)}.box .typedWord{position:absolute;top:0;left:0}.box .right{color:var(--fg-color)}.box #active{position:relative;width:-webkit-max-content;width:max-content}.box span.wrong{color:var(--hl-color) !important}.box .extra{opacity:.6}.box #caret{transition:left .1s ease;margin-left:-7.29165px;position:absolute;color:var(--hl-color)}.box #caret.blink{animation:blink 1.5s infinite 1s}@keyframes blink{0%,100%{opacity:1}50%{opacity:0}}
.dark-cherry{--bg-color: #171717 !important;--font-color: #ededed !important;--hl-color: #da0037 !important;--fg-color: #444444 !important}.dark{--bg-color: #171717 !important;--font-color: #353535 !important;--hl-color: #252525 !important;--fg-color: #444444 !important}.thanksgiving{--bg-color: #A26B35 !important;--font-color: #EBBA38 !important;--hl-color: #C1311C !important;--fg-color: #BDB850 !important}.mocha{--bg-color: #2d2424 !important;--font-color: #e0c097 !important;--hl-color: #dc521c !important;--fg-color: #5c3d2e !important}.coral{--bg-color: #fdd2bf !important;--font-color: #df5e5e !important;--hl-color: #492f10 !important;--fg-color: #e98580 !important}.ocean{--bg-color: #dddddd !important;--font-color: #125d98 !important;--hl-color: #f5a962 !important;--fg-color: #3c8dad !important}.azure{--bg-color: #383e56 !important;--font-color: #eedad1 !important;--hl-color: #f69e7b !important;--fg-color: #d4b5b0 !important}.forest{--bg-color: #334443 !important;--font-color: #fff8df !important;--hl-color: #c6ffc1 !important;--fg-color: #34656d !important}.rose-milk{--bg-color: #ffffff !important;--font-color: #111111 !important;--hl-color: #b33838 !important;--fg-color: #3b8792 !important}.sky{--bg-color: #5fc2ff !important;--font-color: #b4fff6 !important;--hl-color: #96e7ff !important;--fg-color: #81D6FF !important}
header,footer{margin:50px 0;display:flex;width:50%;justify-content:space-between;align-items:center;font-size:12pt;font-weight:700}header .buttons,footer .buttons{display:flex;flex-direction:column;align-items:flex-end}.brand{font-size:21pt;align-self:center}
.bottom-area{width:50%;justify-content:center;position:relative}.bottom-area .hint{display:flex;align-items:center;justify-content:center}.bottom-area .hint kbd{background-color:var(--font-color);color:var(--bg-color);padding:2.5px 5px;margin:10px;border-radius:3px;font-size:9pt}.bottom-area footer{width:100%}.bottom-area footer button,.bottom-area footer>a{font-size:12pt;border:none;width:150px;margin:0;padding:0 10px}.bottom-area footer .contributor-list{display:flex;flex-direction:column;position:absolute;bottom:90px;right:10px;max-height:300%;width:300px;overflow-y:auto;background-color:var(--bg-color);border:1px solid var(--fg-color);box-shadow:5px 5px var(--fg-color);scrollbar-width:thin}.bottom-area footer .contributor-list h2{position:-webkit-sticky;position:sticky;top:0;background-color:var(--bg-color);padding:15px;margin:0}.bottom-area footer .contributor-list .contributor{display:flex;flex-direction:row;padding:10px}.bottom-area footer .contributor-list .contributor img{border-radius:50%;margin:5px}.bottom-area footer .contributor-list .contributor .contributor-details{display:flex;flex-direction:column;justify-content:space-around;padding:5px 15px}.bottom-area footer .contributor-list::-webkit-scrollbar{width:5px}.bottom-area footer .contributor-list::-webkit-scrollbar-thumb{background-color:var(--font-color);border-radius:10px}.bottom-area footer .contributor-list::-webkit-scrollbar-track-piece{background-color:var(--bg-color)}
